class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modules: [
        ClientSideRowModelModule,
        AllModules,
        SetFilterModule,
      ],
      detailCellRenderer: 'myDetailCellRenderer',
      frameworkComponents: { myDetailCellRenderer: DetailCellRenderer },
      columnDefs:[
         {
          headerName: 'Barcode',
          field: 'barcode',
          checkbox: true,
          cellRenderer: 'agGroupCellRenderer',
          minWidth: 300,
        }, {
          headerName: 'Product Name',
          field: 'name',
          minWidth: 300
        }, {
          headerName: 'Price',
          field: 'price'
        },
        {
          headerName: 'Category',
          field: 'category',

          enableRowGroup: true
        }, {
          headerName: 'SubCategory',
          field: 'subcategory',

          enableRowGroup: true
        }, {
          headerName: 'Brand',
          field: 'brand',
          enableRowGroup: true,
          rowGroup: true,
          hide: true,
        }, {
          headerName: 'Type',
          field: 'type',
          sort: 'asc',
          filter: 'agTextColumnFilter',
          enableRowGroup: true,
          rowGroup: true,
          hide: true,
        }, {
          headerName: 'Last Scan',
          field: 'last_scan'
        }, {
          headerName: 'Picture',
          field: 'image',
          hide: true
        },
      ],
      rowData:[
        // { name: 'Toyota', price: 1500, category:'Laban',brand:'Maraie'}
      ],
      selected:false,
      detailRowHeight: 250,
      sideBar:'columns',
      autoGroupColumnDef: {
        headerName: 'Products tree',
        field: 'tree',
        minWidth: 250,
        cellRenderer: 'agGroupCellRenderer',
        cellRendererParams: { checkbox: true },
        // sideBar: 'filters'
      },
    };
  }


  componentDidMount() {
    this.arrange_data(data_to_arrange)
    // console.log(gridApi.getSelectedNodes());
  }



  arrange_data = async (data) => {

    let returned_data = []
    await data.map(item =>(
      item.main_category
      ?
      returned_data.push({
        barcode:item.barcode,
        name:item.name,
        category:item.main_category,
        subcategory:item.sub_category,
        type:item.type,
        brand:item.brand,
        last_scan:item.last_scan_time,
        price:item.price.toFixed(2),
        image:item.image
      })
      :
      null
    ))
    await this.setState({
      rowData:returned_data
    })
  }

  onButtonClick = () => {
    const selectedNodes = this.gridApi.getSelectedNodes()
    const selectedData = selectedNodes.map( node => node.data )
    const selectedDataStringPresentation = selectedData.map( node => node.barcode + ' ' + node.name).join(', ')
    alert(`Selected nodes: ${selectedDataStringPresentation}`)
    this.setState({selected:true})

  }

  closeToolPanel = () => {
    this.gridApi.closeToolPanel();
  };

  onFirstDataRendered = params => {
    params.api.forEachNode(function(node) {
      node.setExpanded(node.id === '1');
    });
  };

  render() {
    return (
        <div className="container" >
          <div style={{marginTop: 10,marginBottom: 10,display: 'flex',flexDirection: 'row',justifyContent: 'space-between',alignItems: 'center',width: '100%'}}>
            {/* <Button onClick={this.onButtonClick} variant="contained" color="primary">
                
            </Button>
            <Button onClick={this.onButtonClick} variant="contained" color="primary">
                
            </Button> */}
          </div>
          <div className="ag-theme-balham" style={{width:'100%',height:800}}>
            <AgGridReact
              onGridReady={
                params => {
                  this.gridApi = params.api
                  this.closeToolPanel()
                }
              }
              columnDefs={this.state.columnDefs}
              rowData={this.state.rowData}
              headerHeight={80}
              rowSelection="multiple"
              rowMultiSelectWithClick= {true}
              gridOptions={{
                defaultColDef:{
                  sortable:true,
                  filter:true,
                  resizable:true,
                  minWidth: 100,
                },
                enableRangeSelection: true,
                animateRows: true
              }}
              detailCellRenderer={this.state.detailCellRenderer}
              frameworkComponents={this.state.frameworkComponents}
              onFirstDataRendered={this.onFirstDataRendered.bind(this)}
              modules={this.state.modules}
              masterDetail={true}
              detailRowHeight={this.state.detailRowHeight}

              autoGroupColumnDef={this.state.autoGroupColumnDef}
              groupSelectsChildren={true}
              sideBar={this.state.sideBar}
            />


          </div>
          <div style={{display: 'flex',flexDirection: 'row',justifyContent: 'flex-end',width: '100%'}} >
            {/* <Fab color="primary" aria-label="add" >
              <AddIcon />
            </Fab> */}
            <Fab icon={"+"} position={{right:0,bottom:0}} alwaysShowTitle={true} event={'click'} mainButtonStyles={{
                backgroundColor: '#6EB536',
              }}>
                <Action text="  " onClick={this.closeToolPanel}/>
              <Action text="  " onClick={this.onButtonClick}>

                </Action>
            </Fab>
          </div>

        </div>
    );
  }

}

export default App;
